import iconAvatar from 'assets/images/avatar_businessman.png';
import ball from 'assets/images/Ball img.png';
import dropD from 'assets/images/dropdownColor.png';
import bg from 'assets/images/gamehome_island_background.png';
import leadboard from 'assets/images/leadboard.png';
import mark from 'assets/images/Mark.png';
import rate from 'assets/images/Rate Group.png';
import shop from 'assets/images/shop.png';
import tool from 'assets/images/tools.png';
import TextBoard from 'components/TextBoard';
import './style.scss';

function PlayGame() {
    return (
        <div className='game-homepage-main' id='121:3666'>
            <div className='tquest-homepage-frame' id='121:3667'>
                <img className='bgimage' src={bg} alt='' />
                <div className='treasurequest-image' id='121:3669'>
                    <TextBoard
                        size='xl'
                        texts={[
                            {
                                text: 'treasure',
                                offset: 147,
                            },
                            {
                                text: 'quest',
                                offset: 250,
                            },
                        ]}
                    />
                </div>
                <div className='dailyC-frame' id='121:3671'>
                    Daily Challenge{' '}
                </div>
                <div className='achiev-frame' id='121:3673'>
                    Achievement
                </div>
                <div className='treasure-frame' id='121:3675'>
                    Treasures
                </div>
                <div className='solo-quest-frame' id='121:3677'>
                    <div className='solo-quest-cont' id='121:3678'>
                        solo-Quest
                    </div>
                    <div className='black-frame' id='121:3685' />
                    <div className='auto-group-1-frame' id='auto-group'>
                        <div className='rules-frame' id='121:3679'>
                            Rules
                        </div>
                        <div className='reward-frame' id='121:3681'>
                            Rewards
                        </div>
                        <div className='reward-frame-cont' id='121:3683'>
                            leorem ipsom
                        </div>
                    </div>
                </div>
                <div className='rev-share-frame' id='121:3686'>
                    <div className='rev-share-cont' id='121:3687'>
                        rev -share
                    </div>
                    <div className='main-entry-frame' id='121:3692'>
                        <div className='sub-entry-frame' id='121:3693'>
                            <div className='entry' id='121:3694'>
                                ENTRY:
                            </div>
                            <div className='num-1' id='121:3695'>
                                1
                            </div>
                            <img
                                className='image-frame'
                                src={mark}
                                alt=''
                                id='121:3696'
                            />
                        </div>
                    </div>
                    <div className='auto-group-2-frame' id='auto-group2'>
                        <div className='rules-frame-2' id='121:3688'>
                            Rules
                        </div>
                        <div className='reward-frame-2' id='121:3690'>
                            Rewards
                        </div>
                        <div className='reward-frame-cont-2' id='121:3703'>
                            Rev share rules
                        </div>
                    </div>
                </div>
                <div className='Profile'>
                    <div className='icon'>
                        <img src={iconAvatar} alt='' />
                    </div>
                    <div className='Tools_frame'>
                        <div className='Sub_tools_frame'>
                            <img src={tool} alt='' />
                        </div>
                        <p className='Tools_paragraph'>Tools</p>
                    </div>
                    <div className='Leadboard_frame'>
                        <img src={leadboard} alt='' />
                        <div className='Leadboard_group_frame'>
                            <p className=''>Leader Board</p>
                            <div className='Leadboard_group_frame1'>
                                <img className='ball' src={ball} alt='' />
                                <p className='content'>
                                    <span className='pound'>#</span>
                                    <span className='Number'>234</span>
                                </p>
                            </div>
                            <p className='rounds'>
                                Round ends in: 5d 23hrs 40s
                            </p>
                        </div>
                    </div>
                    <div className='Shop_frame'>
                        <div className='shop_frame_2'>
                            <div className='logo_frame'>
                                <img className='image' src={shop} alt='' />
                            </div>
                            <p className='shop_paragraph'>
                                Offer lasts: 5d 23hrs 40s
                            </p>
                            <div className='sub_shop_frame2'>
                                <p className='shop_text'>Shop</p>
                                <img className='rate' src={rate} alt='' />
                            </div>
                        </div>
                    </div>
                    <div className='rating_frame1'>
                        <div className='rating_frame4'>
                            <p className='rating_frame_plus'>+</p>
                        </div>
                        <div className='rating_frame2'>
                            <div className='rating_frame3'>76</div>
                        </div>
                    </div>
                    <div className='dropdown_frame'>
                        <p className='stats'>0*23...BED1</p>
                        <p className='name'>JeffWonder01</p>
                        <div className='dropdown'>
                            <img src={dropD} alt='' />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default PlayGame;
