import HomePage from 'containers/HomePage';
import Menu from 'containers/Menu';
import PlayNow from 'page/PlayNow';
import PlayGame from 'pageG/PlayGame';
import ConnectWallet from 'pages/ConnectWallet';
import GameRulesPop from 'pagesrules/GameRulesPop';
import React from 'react';

export interface IRoute {
    url: string;
    component: React.ReactElement;
}

export const Routes: IRoute[] = [
    {
        url: '/',
        component: (
            <>
                <Menu />
                <HomePage />
            </>
        ),
    },
    {
        url: '/connectWallet',
        component: <ConnectWallet />,
    },
    {
        url: '/playNow',
        component: <PlayNow />,
    },
    {
        url: '/gameRulesPop',
        component: <GameRulesPop />,
    },
    {
        url: '/PlayGame',
        component: <PlayGame />,
    },
];
