import iconAvatar from 'assets/images/avatar_businessman.png';
import ball from 'assets/images/Ball img.png';
import dropD from 'assets/images/dropdownColor.png';
import leadboard from 'assets/images/leadboard.png';
import rate from 'assets/images/Rate Group.png';
import shop from 'assets/images/shop.png';
import tool from 'assets/images/tools.png';
import back from 'assets/images/Vector_back.png';
import BackButton from 'components/BackButton';
import TextBoard from 'components/TextBoard';
import './style.scss';

function playNow() {
    return (
        <div className='choose_anisland_container'>
            <div className='top_section'>
                <div className='IslandA1' />
                <div className='IslandA2' />
                <div className='IslandA3' />
                <div className='IslandA4' />
                <div className='IslandA5' />
                <BackButton icon={back} text='Back' />
                <div className='Profile'>
                    <div className='icon'>
                        <img src={iconAvatar} alt='' />
                    </div>
                    <div className='Tools_frame'>
                        <div className='Sub_tools_frame'>
                            <img src={tool} alt='' />
                        </div>
                        <p className='Tools_paragraph'>Tools</p>
                    </div>
                    <div className='Leadboard_frame'>
                        <img src={leadboard} alt='' />
                        <div className='Leadboard_group_frame'>
                            <p className=''>Leader Board</p>
                            <div className='Leadboard_group_frame1'>
                                <img className='ball' src={ball} alt='' />
                                <p className='content'>
                                    <span className='pound'>#</span>
                                    <span className='Number'>234</span>
                                </p>
                            </div>
                            <p className='rounds'>
                                Round ends in: 5d 23hrs 40s
                            </p>
                        </div>
                    </div>
                    <div className='Shop_frame'>
                        <div className='shop_frame_2'>
                            <div className='logo_frame'>
                                <img className='image' src={shop} alt='' />
                            </div>
                            <p className='shop_paragraph'>
                                Offer lasts: 5d 23hrs 40s
                            </p>
                            <div className='sub_shop_frame2'>
                                <p className='shop_text'>Shop</p>
                                <img className='rate' src={rate} alt='' />
                            </div>
                        </div>
                    </div>
                    <div className='rating_frame1'>
                        <div className='rating_frame4'>
                            <p className='rating_frame_plus'>+</p>
                        </div>
                        <div className='rating_frame2'>
                            <div className='rating_frame3'>76</div>
                        </div>
                    </div>
                    <div className='dropdown_frame'>
                        <p className='stats'>0*23...BED1</p>
                        <p className='name'>JeffWonder01</p>
                        <div className='dropdown'>
                            <img src={dropD} alt='' />
                        </div>
                    </div>
                </div>
            </div>
            <TextBoard
                size='xl'
                texts={[
                    {
                        text: 'choose',
                        offset: 210,
                    },
                    {
                        text: 'island',
                        offset: 240,
                    },
                ]}
            />
        </div>
    );
}

export default playNow;
