import classNames from 'classnames';
import { SocialButtonProps } from './interface';
import './styles.scss';

function SocialButton({ icon, url, className }: SocialButtonProps) {
    return (
        <a className={classNames('social-button', className)} href={url}>
            <img src={icon} alt='' />
        </a>
    );
}

export default SocialButton;
