import controllerIcon from 'assets/icons/controller.svg';
import playIcon from 'assets/icons/play.svg';
import banner from 'assets/images/banner.png';
import cloud from 'assets/images/cloud.png';
import Button from 'components/Button';
import TextBoard from 'components/TextBoard';
import { motion } from 'framer-motion';
import { useLoading } from 'providers/LoadingProvider';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
    bannerImageVariants,
    buttonsVariants,
    cloudVariants,
    textBoardVariants,
    textVariants,
} from './animations';
import './styles.scss';

const CLOUDS = Array(8).fill(0);

function Banner() {
    const { loading, setLoading } = useLoading();
    const [cloudLoaded, setCloudLoaded] = useState(false);
    const navigate = useNavigate();

    const handleNavigation = () => {
        navigate('/playNow');
    };
    const handleNavigation1 = () => {
        navigate('/playGame');
    };

    useEffect(() => {
        if (cloudLoaded) {
            setTimeout(() => {
                setLoading!(false);
            }, 1300);
        }
    }, [cloudLoaded]);

    return (
        <motion.div
            className='banner'
            initial='rest'
            whileInView={cloudLoaded ? 'animated' : ''}
            viewport={{ once: true, amount: 0.6 }}
        >
            <motion.img
                className='banner__image'
                src={banner}
                alt=''
                variants={bannerImageVariants}
            />
            <div className='banner__clouds'>
                {CLOUDS.map((e, i) => (
                    <motion.img
                        src={cloud}
                        onLoad={() => setCloudLoaded(true)}
                        alt=''
                        variants={cloudVariants[i]}
                    />
                ))}
            </div>
            <motion.div
                className='banner__content grid-wrapper'
                initial='rest'
                animate={loading ? 'rest' : 'animated'}
            >
                <motion.div
                    className='banner__content__text-board'
                    variants={textBoardVariants}
                >
                    <TextBoard
                        size='xl'
                        texts={[
                            {
                                text: 'treasure',
                                offset: 147,
                            },
                            {
                                text: 'quest',
                                offset: 250,
                            },
                        ]}
                    />
                </motion.div>
                <motion.div
                    className='banner__content__buttons'
                    variants={buttonsVariants}
                >
                    <Button onClick={handleNavigation}>
                        <img src={controllerIcon} alt='' /> PLAY NOW
                    </Button>
                    <Button onClick={handleNavigation1}>
                        <img src={playIcon} alt='' /> WATCH VIDEO
                    </Button>
                </motion.div>
                <motion.p
                    className='banner__content__text text'
                    variants={textVariants}
                >
                    Get rewarded for your hunting skills with gold. Be a land
                    owner and share from the treasures found by hunters on you
                    piece of land.
                </motion.p>
            </motion.div>
        </motion.div>
    );
}

export default Banner;
