/* eslint-disable react/button-has-type */
import { BackButtonProps } from './interface';
import './style.scss';

function BackButton({
    icon,
    onClick,
    text,
    backgroundColor = '#F4BF9A',
}: BackButtonProps) {
    return (
        <button
            className='back-button'
            style={{ backgroundColor: `${backgroundColor}` }}
            onClick={onClick}
        >
            <img alt={`${text}-icon`} src={icon} />
            <span>{text}</span>
        </button>
    );
}

export default BackButton;
