import boardImage from 'assets/images/board.png';
import classNames from 'classnames';
import ReactCurvedText from 'react-curved-text';
import { TextBoardProps, TextBoardSize, TextsType } from './interface';
import './styles.scss';

const getResolvedSize = (size: TextBoardSize) => {
    switch (size) {
        case 'xl':
            return [700, 278];
        case 'm':
        default:
            return [450, 179];
    }
};

const getResolvedFontSize = (size: TextBoardSize, rows: number) => {
    switch (size) {
        case 'xl':
            return 100;
        case 'm':
        default:
            if (rows <= 2) return 63;
            return 45;
    }
};

const getResolvedY = (size: TextBoardSize, texts: TextsType, index: number) => {
    switch (size) {
        case 'xl':
            if (texts.length === 2 && index === 0)
                return 260 + (texts[index].marginTop || 0);
            if (texts.length === 2 && index === 1)
                return 340 + (texts[index].marginTop || 0);
            return 0 + (texts[index].marginTop || 0); // 1 line
        case 'm':
        default:
            if (texts.length === 2 && index === 0)
                return 217 + (texts[index].marginTop || 0);
            if (texts.length === 2 && index === 1)
                return 270 + (texts[index].marginTop || 0);

            if (texts.length === 3 && index === 0)
                return 204 + (texts[index].marginTop || 0);
            if (texts.length === 3 && index === 1)
                return 239 + (texts[index].marginTop || 0);
            if (texts.length === 3 && index === 2)
                return 274 + (texts[index].marginTop || 0);
            return 240 + (texts[index].marginTop || 0); // 1 line
    }
};

const getResolvedValues = (size: TextBoardSize) => {
    switch (size) {
        case 'xl':
            return {
                cx: 350,
                rx: 367,
                ry: 145,
            };
        case 'm':
        default:
            return {
                cx: 225,
                rx: 310,
                ry: 145,
            };
    }
};

function TextBoard({ size = 'm', texts, className, fontSize }: TextBoardProps) {
    const [width, height] = getResolvedSize(size);

    return (
        <div
            className={classNames('text-board', size, className)}
            style={{ width, height }}
        >
            <img src={boardImage} alt='' style={{ width, height }} />
            {texts.map((t, i) => (
                <ReactCurvedText
                    width={width}
                    height={height}
                    cy={getResolvedY(size, texts, i)}
                    {...getResolvedValues(size)}
                    startOffset={t.offset}
                    reversed
                    text={t.text.toUpperCase()}
                    textProps={{
                        style: {
                            fontSize:
                                fontSize ||
                                getResolvedFontSize(size, texts.length),
                        },
                    }}
                    textPathProps={{ fill: '#ffffff' }}
                    tspanProps={{ dy: '1' }}
                    ellipseProps={null}
                    svgProps={{ style: { transform: 'rotate(0deg)' } }}
                />
            ))}
        </div>
    );
}

export default TextBoard;
