import Bitkeep from 'assets/images/Bitkeep.png';
import Blocto from 'assets/images/Blocto.png';
import Coinbase from 'assets/images/coinbase 1.png';
import Ledger from 'assets/images/ledger.png';
import Meta from 'assets/images/Metamask.png';
import Phanton from 'assets/images/phanton2.png';
import Trust from 'assets/images/Vector.png';
import WalletConnect from 'assets/images/Walletconnect.png';
import WalletButton from 'components/WalletButtons';
import { Link } from 'react-router-dom';
import './style.scss';

function connectWallet() {
    return (
        <div className='connect_wallet_container'>
            <h1 className='header'>CONNECT WALLET</h1>
            <h4 className='sub_header'>
                To start, choose one of the wallets below. Be sure to keep your
                private keys or seed phrase securely. Never share them with
                anyone.{' '}
            </h4>
            <div className='wallet_button_group'>
                <WalletButton icon={Meta} text='METAMASK' />
                <WalletButton icon={Trust} text='TRUST WALLET' />
                <WalletButton icon={Coinbase} text='Coinbase' />
                <WalletButton
                    icon={Ledger}
                    text='Ledger'
                    backgroundColor='#3B99FC'
                />
                <WalletButton
                    icon={Blocto}
                    text='Blocto'
                    backgroundColor='#000000'
                />
                <WalletButton icon={WalletConnect} text='WalletConnect' />
                <WalletButton icon={Bitkeep} text='Bitkeep' />
                <WalletButton
                    icon={Phanton}
                    text='Phanton'
                    backgroundColor='#543BCA'
                />
            </div>
            <div className='link_container'>
                <span>
                    {' '}
                    Havent got a crypto wallet yet?
                    <Link to='/'>Generate one now</Link>
                </span>
            </div>
        </div>
    );
}

export default connectWallet;
