import controllerIcon from 'assets/icons/controller.svg';
import background from 'assets/images/banner.png';
import Button from 'components/Button';
import TextBoard from 'components/TextBoard';
import { motion } from 'framer-motion';
import './styles.scss';

function GameRules() {
    return (
        <div className='game-rules'>
            <motion.img className='game-rules__image' src={background} alt='' />
            <div className='grid-wrapper'>
                <TextBoard
                    className='game-rules__text-board'
                    texts={[
                        {
                            text: 'game',
                            offset: 270,
                        },
                        {
                            text: 'rules',
                            offset: 265,
                        },
                    ]}
                />
                <motion.p
                    className='game-rules__text text'
                    // variants={textVariants}
                >
                    Your simple quest is to get as many treasures as you can.
                    There are hidden treasures in 8 out of the 64 plots of land.
                    All you just need to do is to click on any of the plots you
                    feel would hold treasures. If you click on a plot that has
                    no treasure, we can only hope you have better luck in your
                    next attempt.
                </motion.p>
                <motion.div
                    className='game-rules__buttons'
                    // variants={buttonsVariants}
                >
                    <Button>
                        <img src={controllerIcon} alt='' /> REV-SHARE MODE
                    </Button>
                    <Button>
                        <img src={controllerIcon} alt='' /> SOLO QUEST MODE
                    </Button>
                </motion.div>
            </div>
        </div>
    );
}

export default GameRules;
