import classNames from 'classnames';
import { ButtonProps } from './interface';
import './styles.scss';

function Button({ children, onClick, type = 'primary' }: ButtonProps) {
    return (
        <button
            onClick={onClick}
            type='button'
            className={classNames('button', `button--${type}`)}
        >
            {children}
        </button>
    );
}

export default Button;
