import background from 'assets/images/banner.png';
import bitcoinImage from 'assets/images/bitcoin.png';
import cardsImage from 'assets/images/cards.png';
import controllerImage from 'assets/images/controller.png';
import moneyImage from 'assets/images/money.png';
import TextBoard from 'components/TextBoard';
import { motion } from 'framer-motion';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick-theme.css';
import 'slick-carousel/slick/slick.css';
import './styles.scss';

const SLIDER_ITEMS = [
    {
        image: cardsImage,
        title: 'FREE TO PLAY',
        text: 'You dont need a dime in order to earn. Just connect your wallet and start playing. You dont even need to acquire a piece of land before you can play.',
    },
    {
        image: controllerImage,
        title: 'EASY TO PLAY',
        text: 'You dont need a high spec PC or console to play this game, all you need is a mobile phone or PC with internet and your BEP20 compatible wallet and you can immediately start playing.',
    },
    {
        image: bitcoinImage,
        title: 'PASSIVE INCOME',
        text: 'You can earn passive income by acquiring a plot of land. You get to split treasures found by hunters on your piece of land. You dont even have to be online or active to earn.',
    },
    {
        image: moneyImage,
        title: 'EARNING OPPORTUNITY',
        text: 'Play-to-earn indeed. You can earn by joining the treasure quest. Its a win-win situation for both the hunters and the land owners. You share revenue with land owners, in turn they let you plunder their land.',
    },
];

function WhyShouldJoin() {
    return (
        <div className='why-should-join'>
            <motion.img
                className='why-should-join__image'
                src={background}
                alt=''
            />
            <div className='grid-wrapper'>
                <TextBoard
                    className='why-should-join__text-board'
                    fontSize={38}
                    texts={[
                        {
                            text: 'why you should',
                            offset: 190,
                        },
                        {
                            text: 'join the quest',
                            marginTop: -10,
                            offset: 210,
                        },
                    ]}
                />
                <Slider
                    dots={false}
                    className='why-should-join__slider'
                    arrows={false}
                    infinite
                    speed={500}
                    slidesToShow={3}
                    slidesToScroll={1}
                    autoplaySpeed={5000}
                    autoplay
                >
                    {SLIDER_ITEMS.map(({ image, title, text }) => (
                        <div className='why-should-join__slider__item-border-wrap'>
                            <div className='why-should-join__slider__item'>
                                <img src={image} alt='' />
                                <div className='why-should-join__slider__item__title'>
                                    {title}
                                </div>
                                <div className='why-should-join__slider__item__text'>
                                    {text}
                                </div>
                            </div>
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    );
}

export default WhyShouldJoin;
