import background from 'assets/images/banner.png';
import TextBoard from 'components/TextBoard';
import { motion } from 'framer-motion';
import './styles.scss';

function About() {
    return (
        <div className='about'>
            <motion.img className='about__image' src={background} alt='' />
            <div className='grid-wrapper'>
                <TextBoard
                    className='about__text-board'
                    texts={[
                        {
                            text: 'about',
                            offset: 290,
                        },
                        {
                            text: 'treasure',
                            offset: 247,
                        },
                        {
                            text: 'quest',
                            offset: 290,
                        },
                    ]}
                />
                <motion.p
                    className='about__text text'
                    // variants={textVariants}
                >
                    Treasure Quest is a Play-to-earn (P2E) game that rewards
                    your treasure hunting skills with gold. You can either dig
                    for treasure on your own piece of land or plunder other
                    users land, however...
                </motion.p>
            </div>
        </div>
    );
}

export default About;
