import migbg from 'assets/images/Sao Miguel Itinerary2.png';
import './style.scss';

function gameRulesPop() {
    return (
        <div className='game_rules_container'>
            <div className='game_rules_img'>
                <img src={migbg} alt='' />
            </div>
            <div className='game_rules_header_frame'>
                <h4 className='header'>Game Rules</h4>
            </div>
            <div className='game_rules_body'>
                <p className='body'>
                    Your simple quest is to get as many treasures as you can.
                    You select an Island where you want to start your quest. The
                    plots of land on each island differ. There are hidden
                    treasures and keys in some plots of land. All you just need
                    to do is to click on any of the plots you feel would hold
                    treasures. If you click on a plot that has no treasure, you
                    can only hope to have better luck in your next attempt. You
                    have limited attempts per game depending on the island
                    youre hunting on. After exhausting all your attempts, the
                    game ends and then you can claim your treasures or key (in
                    the form of $QUEST token or key respectively). There are 3
                    kinds of treasures; Gold, Platinum and Diamond. There would
                    be treasure chests available to be unlocked with the keys
                    you find on your quest. These chests contain Platinum. Also
                    note that each of the plots belong to someone. Whatever
                    treasure you find in your quest will be split evenly between
                    yourself and the square owners (after 10% Foundation
                    commission). A player can play this game mode only 5 times
                    within 24 hours. However, if your hunger for treasure is not
                    satisfied after playing just 5 times, you can opt for the
                    Solo Quest Mode below. If you select this option, you have
                    unlimited attempts to play. You also would not share only
                    about with land owners, 100% of your acquired treasures
                    (minus 10% Foundation commission) will be yours. You need to
                    pay an entry fee in $QUEST for each game in order to have
                    access to the Solo Quest Mode.
                </p>
            </div>
        </div>
    );
}

export default gameRulesPop;
