import discordIcon from 'assets/icons/discord.svg';
import twitterIcon from 'assets/icons/twitter.svg';
import Button from 'components/Button';
import SocialButton from 'components/SocialButton';
import { motion } from 'framer-motion';
import { useLoading } from 'providers/LoadingProvider';
import { Trans, useTranslation } from 'react-i18next';
import { Link, useNavigate } from 'react-router-dom';
import { menuVariants } from './animations';
import { Link as ILink } from './interface';
import './styles.scss';

const LINKS: ILink[] = [
    {
        text: <Trans i18nKey='menu.HOME' />,
        url: '/',
    },
    {
        text: <Trans i18nKey='menu.EARN' />,
        url: '/#earn',
    },
    {
        text: <Trans i18nKey='menu.ROADMAP' />,
        url: '/#roadmap',
    },
    {
        text: <Trans i18nKey='menu.ABOUT_US' />,
        url: '/#aboutus',
    },
];

function Menu() {
    const { loading } = useLoading();
    const { t } = useTranslation();
    const navigate = useNavigate();

    const handleNavigation = () => {
        navigate('/connectwallet');
    };

    return (
        <motion.div
            className='menu'
            initial='rest'
            animate={loading ? 'rest' : 'animated'}
            variants={menuVariants}
        >
            <div>
                <div className='menu__items'>
                    {LINKS.map(({ text, url }) => (
                        <Link key={url} to={url} className='menu__items__item'>
                            {text}
                        </Link>
                    ))}
                </div>
            </div>
            <div className='menu-right'>
                <SocialButton icon={discordIcon} url='/' />
                <SocialButton icon={twitterIcon} url='/' />
                <Button onClick={handleNavigation}>
                    {t('menu.CONNECT_WALLET')}
                </Button>
            </div>
        </motion.div>
    );
}

export default Menu;
